import createRangeMesh from '../rangeMesh';

// three js was imported in index.html
// eslint-disable-next-line no-undef
let three = THREE;

export default function buildMathSideTest(scene) {
  let material = new three.MeshNormalMaterial();

  let mesh = createRangeMesh([1,1,1], [2,2,2], material);
  let mesh2 = createRangeMesh([1,2,2], [3,3,3], material);

  scene.add(mesh);
  scene.add(mesh2);
}



