// three js was imported in index.html
// eslint-disable-next-line no-undef
let three = THREE;


export function buildBoxRelic(props) {
  let {
    matrixSize, distance, movingDistance, emptyBoxChance, stayBoxChance,
    unlockImmediately, spinningDistanceStep, addSpinningEffectChance,
    targetScene, meshContext
  } = props;

  let boxList = [];

  let geometry = new three.BoxGeometry(0.1, 0.1, 0.1);
  let material = new three.MeshNormalMaterial();

  // if camera not positioned to center of building, this will little help
  let axisMinimum = -1 * Math.floor(matrixSize / 2);
  let axisMaximum = axisMinimum + matrixSize;

  let fillMatrix = {
    minimalIndex: axisMinimum,
    maximalIndex: axisMaximum - 1
  };
  for (let i = axisMinimum; i < axisMaximum; i++) {
    fillMatrix[i] = {};
    for (let j = axisMinimum; j < axisMaximum; j++) {
      fillMatrix[i][j] = {};
      for (let k = axisMinimum; k < axisMaximum; k++) {
        if (Math.random() > emptyBoxChance) {
          fillMatrix[i][j][k] = true;
          let mesh = new three.Mesh(geometry, material);
          mesh.position.set(distance * i, distance * j, distance * k);
          let id = Math.random().toString(36).substring(7);
          let position = [i, j, k];
          boxList.push({mesh, meshId: id, position});
          targetScene.add(mesh);
        } else {
          fillMatrix[i][j][k] = false;
        }
      }
    }
  }

  meshContext.boxList = boxList;
  meshContext.fillMatrix = fillMatrix;
  meshContext.distance = distance;
  meshContext.movingDistance = movingDistance;
  meshContext.stayBoxChance = stayBoxChance;
  meshContext.unlockImmediately = unlockImmediately;
  meshContext.spinningDistanceStep = spinningDistanceStep;
  meshContext.addSpinningEffectChance = addSpinningEffectChance;
}

function deepClone(obj) {
  if (!obj) {
    throw new Error('Can not clone empty link');
  }

  return JSON.parse(JSON.stringify(obj));
}

function changePosition(position, deltas) {
  return deepClone([
    position[0] + deltas[0],
    position[1] + deltas[1],
    position[2] + deltas[2]
  ]);
}

function isPositionValid(position, minIndex, maxIndex) {
  let x = position[0];
  let y = position[1];
  let z = position[2];

  if (x < minIndex || x > maxIndex) return false;
  if (y < minIndex || y > maxIndex) return false;
  if (z < minIndex || z > maxIndex) return false;

  return true;
}

function getAjacentPositions(position, minIndex, maxIndex) {
  let deltas = [
    [+1, 0, 0], [0, +1, 0], [0, 0, +1],
    [-1, 0, 0], [0, -1, 0], [0, 0, -1]
  ];

  let result = [];
  deltas.forEach(delta => {
    let newPosition = changePosition(position, delta);
    if (isPositionValid(newPosition, minIndex, maxIndex)) {
      result.push(newPosition);
    }
  });

  return result;
}

function getAjacentEmptyPlaces(position, fillMatrix) {
  let allAjacentPositions = getAjacentPositions(
    position,
    fillMatrix.minimalIndex,
    fillMatrix.maximalIndex
  );

  // TODO 1: make functional way
  let result = [];
  allAjacentPositions.forEach(ajacentPosition => {
    let x = ajacentPosition[0];
    let y = ajacentPosition[1];
    let z = ajacentPosition[2];
    if (!fillMatrix[x][y][z]) {
      result.push(ajacentPosition);
    }
  });

  return result;
}

function getRandomFromArray(items) {
  return items[Math.floor(Math.random() * items.length)];
}

function addMoveEffectIfPossible(position, fillMatrix) {
  let ajacentEmptyPlaces = getAjacentEmptyPlaces(position, fillMatrix);
  if (ajacentEmptyPlaces.length > 0) {
    let targetPosition = getRandomFromArray(ajacentEmptyPlaces);
    return {effectType: 'move', targetPosition};
  }
}

function addMoveEffectWithChance(addChance) {
  if (!addChance) throw new Error('missing addChance argument');

  if (Math.random() > addChance) return;

  let spinningAxis = getRandomAxis();
  return {effectType: 'spin', spinningAxis};
}

function getRandomAxis() {
  return getRandomFromArray(['x', 'y', 'z']);
}

function round100(number) {
  return Number((number).toFixed(2));
}

let effectMeshes = {};
export function animateRelicBoxes(meshContext) {
  let {
    boxList, fillMatrix, distance, movingDistance, stayBoxChance,
    unlockImmediately, spinningDistanceStep, addSpinningEffectChance
  } = meshContext;
  boxList.forEach(box => {
    let { meshId, mesh, position } = box;
    let effectInfo = effectMeshes[meshId];
    if (effectInfo) { // if mesh moving (or spinning)  right now
      if (effectInfo.effectType === 'move') {
        let { targetPosition, directions } = effectInfo;

        if (!directions) {
          let xDirection = position[0] < targetPosition[0] ? 1 : -1;
          let yDirection = position[1] < targetPosition[1] ? 1 : -1;
          let zDirection = position[2] < targetPosition[2] ? 1 : -1;
          effectInfo.directions = [xDirection, yDirection, zDirection];
        }

        if (position[0] !== targetPosition[0]) {
          position[0] = round100(position[0] + movingDistance * effectInfo.directions[0]);
        }
        if (position[1] !== targetPosition[1]) {
          position[1] = round100(position[1] + movingDistance * effectInfo.directions[1]);
        }
        if (position[2] !== targetPosition[2]) {
          position[2] = round100(position[2] + movingDistance * effectInfo.directions[2]);
        }
        mesh.position.set(
          distance * position[0],
          distance * position[1],
          distance * position[2]
        );

        if (
          position[0] === targetPosition[0] &&
          position[1] === targetPosition[1] &&
          position[2] === targetPosition[2]
        ) {
          if (!unlockImmediately) {
            let { oldCoordinates } = effectInfo;
            fillMatrix[oldCoordinates[0]][oldCoordinates[1]][oldCoordinates[2]] = false;
          }

          delete effectMeshes[meshId];
        }
      }

      if (effectInfo.effectType === 'spin') {
        let {spinningAxis} = effectInfo;
        mesh.rotation[spinningAxis] += spinningDistanceStep;

        if (mesh.rotation[spinningAxis] > 6.25) {
          mesh.rotation[spinningAxis] = 0;
          delete effectMeshes[meshId];
        }
      }

    } else { // if mesh NOT moving (or spinning) right now
      if (Math.random() > stayBoxChance) {
        let optionalEffectInfo = addMoveEffectIfPossible(position, fillMatrix);
        if (!optionalEffectInfo) {
          optionalEffectInfo = addMoveEffectWithChance(addSpinningEffectChance);
        }

        if (optionalEffectInfo) {
          effectMeshes[meshId] = optionalEffectInfo;

          if (optionalEffectInfo.effectType === 'move') {
            let oldX = position[0];
            let oldY = position[1];
            let oldZ = position[2];

            let { targetPosition } = optionalEffectInfo;
            let newX = targetPosition[0];
            let newY = targetPosition[1];
            let newZ = targetPosition[2];

            if (unlockImmediately) {
              fillMatrix[oldX][oldY][oldZ] = false;
            } else {
              optionalEffectInfo.oldCoordinates = [oldX, oldY, oldZ];
            }

            fillMatrix[newX][newY][newZ] = true;
          }

          if (optionalEffectInfo.effectType === 'spin') {
            // nothing to do here
          }
        }
      }
    }
  });
}



