// three js was imported in index.html
// eslint-disable-next-line no-undef
let three = THREE;
let {
  BoxGeometry: Box,
  ConeGeometry: Cone,
  CylinderGeometry: Cylinder,
  Mesh
} = three;


export default function buildLegoBohrokSpinner(scene) {
  let material = new three.MeshNormalMaterial();

  let verticalMesh1 = new Mesh(new Box(0.17, 1.85, 0.5), material);
  let verticalMesh2 = new Mesh(new Box(0.5, 1.15, 0.17), material);
  verticalMesh2.position.set(0, -0.35, 0);
  scene.add(verticalMesh1);
  scene.add(verticalMesh2);

  let bottomCylinder = new Mesh(new Cylinder(0.40, 0.405, 0.1, 32), material);
  bottomCylinder.position.set(0, -0.2, 0);
  scene.add(bottomCylinder);

  let middleTopCylinderGeometry = new Cylinder(0.38, 0.38, 0.09, 32);

  let middleCylinder = new Mesh(middleTopCylinderGeometry, material);
  middleCylinder.position.set(0, 0, 0);
  scene.add(middleCylinder);

  let topCylinder = new Mesh(middleTopCylinderGeometry, material);
  topCylinder.position.set(0, 0.2, 0);
  scene.add(topCylinder);

  let headCylinder = new Mesh(new Cylinder(0.405, 0.405, 0.6, 32), material);
  headCylinder.rotation.set(0, 0, 1.55);
  headCylinder.position.set(0, 0.68, 0);
  scene.add(headCylinder);

  let noseCone = new Mesh(new Cone(0.405, 0.3, 32), material);
  noseCone.position.set(-0.45, 0.69, 0);
  noseCone.rotation.set(0, 0, 1.55);
  scene.add(noseCone);

  let throat = new Mesh(new Box(0.3, 0.17, 0.15), material);
  throat.position.set(-0.22, 0.3, 0);
  scene.add(throat);

  // TODO: why not try it: https://github.com/mrdoob/three.js/issues/866
  let middleCutCylinder = createMiddleCutCylinder(material);
  scene.add(middleCutCylinder);
}

function createMiddleCutCylinder(material) {
  let middleCutCylinder = new Mesh(new Cylinder(0.09, 0.09, 0.235, 32), material);
  middleCutCylinder.position.set(0, 0, 0.25);
  middleCutCylinder.rotation.set(1.55, 0, 0);
  return middleCutCylinder;
}



