import {evalDeltas, evalMinimals} from './common';

// three js was imported in index.html
// eslint-disable-next-line no-undef
let three = THREE;

export default function createRangeMesh(fromCoord, toCoord, material) {
  let {deltaX, deltaY, deltaZ} = evalDeltas(fromCoord, toCoord);
  debugger;  
  let {minX, minY, minZ} = evalMinimals(fromCoord, toCoord);
  debugger;

  let geometry = new three.BoxGeometry(deltaX, deltaY, deltaZ);
  let mesh = new three.Mesh(geometry, material);
  mesh.position.set(minX, minY, minZ);

  return mesh;
}



