import {getRandomBetween} from '../common';

// three js was imported in index.html
// eslint-disable-next-line no-undef
let three = THREE;

function createRandomStick({
  bigSizeMin, bigSizeMax, smallSize,
  minOffset, maxOffset, material
}) {
  let bigSize = getRandomBetween(bigSizeMin, bigSizeMax);
  let ySize = smallSize;
  let xSize, zSize;
  if (Math.random() >= 0.5) {
    xSize = bigSize;
    zSize = smallSize;
  } else {
    zSize = bigSize;
    xSize = smallSize;
  }

  let geometry = new three.BoxGeometry(xSize, ySize, zSize);
  let mesh = new three.Mesh(geometry, material);

  let xOffset = getRandomBetween(minOffset, maxOffset);
  let yOffset = getRandomBetween(minOffset, maxOffset);
  let zOffset = getRandomBetween(minOffset, maxOffset);
  mesh.position.set(zOffset, yOffset, zOffset);

  return mesh;
}

export default function buildRelic(targetScene) {
  let meshList = [];

  let minOffset = -0.5;
  let maxOffset = 0.5;
  let meshCount = 1000;

  let material = new three.MeshNormalMaterial();
  
  for (let i = 0; i < meshCount; i++) {
    let mesh = createRandomStick({
      bigSizeMin: 1, bigSizeMax: 5, smallSize: 0.01,
      minOffset,
      maxOffset,
      material
    });
    meshList.push(mesh);
  }

  let meshIdx = 0;
  let placeMeshIntervalId = setInterval(() => {
    if (meshIdx === meshList.length) {
      clearInterval(placeMeshIntervalId);
    }

    let mesh = meshList[meshIdx];
    targetScene.add(mesh);
    meshIdx++;
  }, 50);
}



